import React from "react"
import styled from "styled-components"
import { Layout } from "../components/layout"
import { SubTitleBold, ParagraphBold } from "../components/textstyling"
import { Link } from "gatsby"

const Content = styled.div`
  grid-area: content;
  height: 120px;
`
const ErrorTitle = styled(SubTitleBold)`
  color: #e5e5e5;
  width: 100%;
  text-align: center;
  margin-top: 250px;
`

const LinkToHomePage = styled(Link)``

const ErrorTitleParagraph = styled(ParagraphBold)`
  width: 100%;
  text-align: center;
  color: #7b7575;
  margin-top: 15px;
  cursor: pointer;

  :hover {
    text-decoration: underline;
    color: #464141;
  }
`

const MissingLink = styled.img`
  position: absolute;
  top: 50%;
  left: 50%;
  width: 14%;
  height: 14%;
  margin-top: -7%;
  margin-left: -7%;
`

export default () => (
  <Layout>
    <Content>
      <MissingLink src="../404.svg" alt="ICON_ERROR" />
      <ErrorTitle> 404 - page not found </ErrorTitle>
      <LinkToHomePage>
        <ErrorTitleParagraph to="/">
          GO CHECK OUT SOME ICONS
        </ErrorTitleParagraph>
      </LinkToHomePage>
    </Content>
  </Layout>
)
